import './App.css';
import { useState } from 'react';

import ReactMarkdown from 'react-markdown'
import rehypeHighlight from 'rehype-highlight'

function App() {

  const [prompt, setPrompt] = useState("");
  const [response, setResponse] = useState("");
  function handleOnChange(event) {
    event.preventDefault();
    setPrompt(event.target.value);
  }

  function handleOnClick(event) {

    const {
      GoogleGenerativeAI,
      HarmCategory,
      HarmBlockThreshold,
    } = require("@google/generative-ai");

    const MODEL_NAME = "gemini-pro";
    // const API_KEY = process.env.REACT_APP_API_KEY || 'default-API-key';
    let API_KEY = process.env.REACT_APP_API_KEY;
    if(!API_KEY) {
      // fetch api key from server running at port 3030
      fetch('http://localhost:3030/')
        .then(response => response.json())
        .then(data => {
          API_KEY = data["apiKey"]
          console.log("API_KEY: ", API_KEY)
        })
        .catch(error => console.error(error))

    } 

    async function runChat() {
      const genAI = new GoogleGenerativeAI(API_KEY);
      const model = genAI.getGenerativeModel({ model: MODEL_NAME });

      const generationConfig = {
        temperature: 0.9,
        topK: 1,
        topP: 1,
        maxOutputTokens: 2048,
      };

      const safetySettings = [
        {
          category: HarmCategory.HARM_CATEGORY_HARASSMENT,
          threshold: HarmBlockThreshold.BLOCK_MEDIUM_AND_ABOVE,
        },
        {
          category: HarmCategory.HARM_CATEGORY_HATE_SPEECH,
          threshold: HarmBlockThreshold.BLOCK_MEDIUM_AND_ABOVE,
        },
        {
          category: HarmCategory.HARM_CATEGORY_SEXUALLY_EXPLICIT,
          threshold: HarmBlockThreshold.BLOCK_MEDIUM_AND_ABOVE,
        },
        {
          category: HarmCategory.HARM_CATEGORY_DANGEROUS_CONTENT,
          threshold: HarmBlockThreshold.BLOCK_MEDIUM_AND_ABOVE,
        },
      ];

      const chat = model.startChat({
        generationConfig,
        safetySettings,
        history: [
          {
            role: "user",
            parts: [{ text: "you are an expert in creating JavaScript applications for client-side and server-side applications. Your role is to explain, generate, review, debug and document only javascript code as required. If any questions outside your role are asked, politely refuse. Also politely refuse, if questions or explanations programming languages other than JavaScript are asked" }],
          },
          {
            role: "model",
            parts: [{ text: "Ok" }],
          },
          {
            role: "user",
            parts: [{ text: "Print \"Hello World\" in JavaScript" }],
          },
          {
            role: "model",
            parts: [{ text: "console.log(\"Hello World\")" }],
          },
          {
            role: "user",
            parts: [{ text: "write javascript code to add two numbers and print the sum" }],
          },
          {
            role: "model",
            parts: [{ text: "const number 1 = 100\nconst number 2 = 200\nconst total = number1 + number2\nconsole.log(`Sum of ${number1} and ${number2} is ${total}`)" }],
          },
          {
            role: "user",
            parts: [{ text: "write javascript function to return true if number is even else return false" }],
          },
          {
            role: "model",
            parts: [{ text: "function check_odd_even(number) {\nif(number % 2 === 0)\nreturn true;\nelse \nreturn false;\n}" }],
          },
          {
            role: "user",
            parts: [{ text: "write javascript function to count vowels in a given string and return the count" }],
          },
          {
            role: "model",
            parts: [{ text: "function count_vowels(string) {\nlet count = 0;\nstring = string.toLowerCase();\nfor (let i = 0; i < string.length; i++) {\nif (string.charAt(i) === 'a' || string.charAt(i) === 'e' || string.charAt(i) === 'i' || string.charAt(i) === 'o' || string.charAt(i) === 'u') {\ncount++;\n}\n}\nreturn count;\n}" }],
          },
        ],
      });
      if (prompt.length === 0) {
        setResponse("")
        return;
      }
      const result = await chat.sendMessage(prompt);
      const response = result.response;
      setResponse(response.text);
      // console.log(response.text);
      // const formattedCode = prettier.format(`const a = "abc";  const b = 'a'  console.log (a);`, {
      //   parser: "babel",
      //   plugins: [babylon],
      // })
      // console.log(formattedCode);
      // setResponse(formattedCode); 
    }

    runChat();

  }

  return (
    <div className="App">
      <header className="App-header">

        <h3>JS-Code-Gen</h3>
        <h6><i>Your Personal Assistant for Generating, Testing, Debugging, Reviewing and Documenting JavaScript Code</i></h6>

      </header>
      <main className="App-main">
        <div className="Prompt-input">
          <textarea rows="4" cols="150" placeholder="Enter Prompt" onChange={handleOnChange} ></textarea>
          <button onClick={handleOnClick} className="Prompt-send-button">Send</button>
        </div>
        <ReactMarkdown rehypePlugins={[rehypeHighlight]} className="Prompt-response" >
          {response.length > 0 ? `${response}` : "The generated response will appear here"}
        </ReactMarkdown>
      </main>
      <footer className="App-footer">
        <small>You are running this application in <b>{process.env.NODE_ENV}</b> mode.</small>
      </footer>

    </div>
  );
}

export default App;
